<template>    
    <!--<v-col gutters sm="6" md="4" lg="4" xl="3" style="background-color: #14101a">-->
        <v-card class="pa-4" style="background-color: #1a1d21;" outlined>
            <v-icon style="float:left;padding-right: 10px;">mdi-graph</v-icon>
            <p style="">Facebook Demographic</p>
            <ul style="list-style: none; width:50%;">
                <li>
                    <img style="filter: invert(1)" width="35" src="@/assets/images/gender-female.png" />
                    <span style="margin-left: 10px; position: absolute; margin-bottom: 5px; line-height: 40px">
                        {{facebookDemographic.femalePercentage}}%
                    </span>
                </li>
                <li>
                    <img style="filter: invert(1)" width="35" src="@/assets/images/gender-male.png" />
                    <span style="margin-left: 10px; position: absolute; margin-bottom: 5px; line-height: 40px">
                        {{facebookDemographic.malePercentage}}%
                    </span>
                </li>
                <li>
                    <img style="filter: invert(1)" width="35" src="@/assets/images/gender-male-female.png" />
                    <span style="margin-left: 10px; position: absolute; margin-bottom: 5px; line-height: 40px">
                        {{facebookDemographic.uniPercentage}}%
                    </span>
                </li>
            </ul>
        </v-card>
    <!--</v-col>-->    
</template>


<script lang="ts">
    //interface IFacebookDemographic {
    //    femalePercentage: number;
    //    malePercentage: number;
    //    uniPercentage: number;
    //}
    

    export default {
        name: 'FacebookDemographic',
        props: {
            facebookDemographic: [] 
        },
        methods: {
            async getFacebookDemographic() {
                console.log('getFacebookDemographic API call');
                const response = await fetch("https://dev.analytics.api.push.live/Facebook/GetDemographicGenderRatio/1", {
                    method: 'GET',
                    cache: 'no-cache',
                    headers: {
                        'Content-Type': 'application/json'
                        },
                    }
                );                
                this.facebookDemographic = await response.json();
                console.log('getFacebookDemographic response ', this.facebookDemographic );
            }
        }, created() {
            this.getFacebookDemographic();
        }
    }
</script>

