<template>
    <v-col gutters cols="12" sm="6" md="8" lg="8" xl="9" style="background-color: #14101a">
        <v-card class="pa-4" outlined style="background-color:#1a1d21;padding-bottom: 32px!important;">
            <v-icon style="float:left;padding-right: 10px;">mdi-chart-bell-curve</v-icon>
            <div>
                <div style="width:50%">
                    <p>YouTube Live Views (Via JS code)</p>
                </div>
            </div>
            <v-container fluid>
                <v-sparkline :fill="fill"
                             :gradient="gradient"
                             :line-width="width"
                             :padding="padding"
                             :smooth="radius || false"
                             :value="youtubeLiveViewsData"
                             auto-draw>
                </v-sparkline>
            </v-container>
            <v-divider></v-divider>
            <div style="float:right; height: 2em">
                <v-btn v-for="(btnItem, index) in btnItems" :key="btnItem" 
                       class="time-range"                       
                       :style="styleSelected(index)"
                       @click="setData(index)"
                       elevation="3"
                       small
                       :disabled="btnItem.disabled">
                    {{btnItem.text}}
                </v-btn>

            </div>
        </v-card>
    </v-col>
</template>

<script lang="ts">

    export default {
        name: 'YouTubeLiveViews',
        data: () => ({
            gradient: ['#00c6ff', '#F0F', '#FF0'],
            youtubeLiveViewsData: [],
            padding: 2,
            radius: 4,
            width: 0.4,
            fill: false,
            backgroundColor: { },            
            btnItems: [
                { text: "1 min" },
                { text: "5 mins" },
                { text: "10 mins" },
                { text: "1 hr", disabled:false },
                { text: "4 hr's", disabled: true },
                { text: "1 Day", disabled: true },
                { text: "7 Day's", disabled: true }
            ]
        }),
        methods: {
            setData: function (interval) {
                this.youtubeLiveViewsData = [];
                this.getYoutubeLiveViewsData(interval);

            },
            styleSelected(i) {
                return {
                    backgroundColor: "#673ab7!important;",
                    index: i
                }
            },
            async getYoutubeLiveViewsData(frequency) {
                try {
                    const response = await fetch("https://dev.analytics.api.push.live/YouTube/GetLiveViews/" + "c2b5c607-8510-4209-9e0f-67176e8fb148" + "/" + frequency, {
                        method: 'GET',
                        cache: 'no-cache',
                        headers: {
                            'Content-Type': 'application/json'
                        },
                    }
                    );
                    const data = await response.json();
                    //this.youtubeLiveViewsData = data.map((v, i, array) => array.push((x) => x.views));
                    for (var i = 0; i < data.length; i++) {
                        this.youtubeLiveViewsData.push(data[i].views)
                    }
                    console.log('al3', this.youtubeLiveViewsData);
                } catch (error) {
                    console.log('Error ', error);
                }
            },

            async GetLatestViews() {
                //let lastElement = this.youtubeLiveViewsData[this.youtubeLiveViewsData.length - 1];
                //console.log('lastElement ', lastElement);
                try {
                    //const fakeResonse = await fetch("https://dev.analytics.api.push.live/YouTube/CreateOneNewFakeLiveView/" + lastElement, {
                    //    method: 'GET',
                    //    cache: 'no-cache',
                    //    headers: {
                    //        'Content-Type': 'application/json'
                    //    },
                    //}
                    //);
                    //const data2 = await fakeResonse.json();
                    //console.log('fakeResponse ', data2.views);

                    //this.youtubeLiveViewsData.push(data2.views);

                } catch (error) {
                    //console.log('Error ', error);
                }
            },

            pollData() {
                this.polling = setInterval(() => {
                    this.GetLatestViews();
                }, 15000);
            }

        },
        watch: {

        },
        created() {
            this.getYoutubeLiveViewsData(1);
            this.pollData();
        },
        mounted() {
            // this is when defind hooks
        }
    }
</script>

<style scoped>
    .range-btn {
        background-color: #673ab7 !important
    }
</style>




